const boardWrap = document.getElementById('boardWrap');
const foo = document.getElementById('foo');
let fooX = 1;
let fooY = 1;
const rowLength = 8;
let isTileLight = true;
const tiles = [];
let x = 1;
let y = 1;
const distanceUnit = 100;
let selected = false;
let possibleMoves = [];

for (let i = 1; i <= 64; i++) {
  const tile = document.createElement('div');
  x += 1;
  if ((i - 1) % rowLength === 0) {
    x = 1;
    y = i === 1 ? 1 : y + 1;
  } else {
    isTileLight = !isTileLight;
  }
  tile.classList += 'tile';
  if (isTileLight) {
    tile.style.backgroundColor = 'white';
  } else {
    tile.style.backgroundColor = 'black';
  }
  tile.innerHTML = i;
  boardWrap.appendChild(tile);
  tiles.push({
    domEl: tile,
    x,
    y,
  });
}

const canMove = function canMove() {
  possibleMoves = [];
  for (const tile of tiles) {
    if ((tile.x === fooX) && (tile.y === fooY)) {
      const moves = [{
        x: tile.x + 2,
        y: tile.y + 1,
      },
      {
        x: tile.x + 2,
        y: tile.y - 1,
      },
      {
        x: tile.x + 1,
        y: tile.y + 2,
      },
      {
        x: tile.x + 1,
        y: tile.y - 2,
      },
      {
        x: tile.x - 1,
        y: tile.y - 2,
      },
      {
        x: tile.x - 2,
        y: tile.y - 1,
      },
      {
        x: tile.x - 1,
        y: tile.y + 2,
      },
      {
        x: tile.x - 2,
        y: tile.y + 1,
      },
      ];
      
      possibleMoves.push(...moves);
    }
  }
};

function doMove(x, y) {
  let toSubstractX = distanceUnit;
  let toSubstractY = distanceUnit;
  let toMultiplyX = x;
  let toMultiplyY = y;
  if (x === 1) {
    toMultiplyX = 0;
    toSubstractX = 0;
  }
  if (y === 1) {
    toMultiplyY = 0;
    toSubstractY = 0;
  }
  foo.style.top = `${(distanceUnit * toMultiplyY) - toSubstractY}px`;
  foo.style.left = `${(distanceUnit * toMultiplyX) - toSubstractX}px`;
  fooX = x;
  fooY = y;
  canMove();
}

canMove();

foo.addEventListener('click', () => {
  selected = true;
});

boardWrap.addEventListener('click', (event) => {
  for (const tile of tiles) {
    if (tile.domEl === event.target && selected === true) {
      for (const move of possibleMoves) {
        if ((move.x === tile.x) && (move.y === tile.y)) {
          doMove(tile.x, tile.y);
        }
      }

      selected = false;
    }
  }
});



